import s from "./sidebar.module.css";
import store from "../../redux/redux.store";
import M from "materialize-css";

//creaters

import {resetSitsActionCreater} from "../../redux/display.reducer";
import {HandleModalActionCreater} from "../../redux/modal.reducer";


const Sidebar = ({reservedSits, freesits}) => {

    const reset = () => {
        M.toast({html: "R*set everything"})
        const action = resetSitsActionCreater();
        store.dispatch(action);
    }

    const openModal = () => {
        console.log("sits", reservedSits);
        if(!reservedSits.price) {
            M.toast({html: "you have to pick at least on sit. But better you pick 2"});
        } else{
            const action = HandleModalActionCreater(reservedSits);
            store.dispatch(action)
        }

    }

    return(
        <div className={s.sidebar}>
           <h1 className={s.title}>Your s*xy Order</h1>
            <div className={s.order}>
                <div>{reservedSits.sits.Loge + reservedSits.sits.Parkett} sits, {reservedSits.sits.Loge} Loge, {reservedSits.sits.Parkett} Parkett</div>
                -------------------------------------------
                <div>Total Price {reservedSits.price}</div>
                __________________________________________
            </div>
            <div className={s.buttons}>
                <button onClick={openModal} className={s.button}>Place my O**er</button>
                <button onClick={reset} className={s.button}>Reset all</button>
            </div>
        </div>


    )
}

export default Sidebar;