import Sidebar from "../Sidebar/Sidebar";
import Display from "../Display/Display";

const Main = ({state, dispatch}) => {


    return(
        <div className="main_content">
            <Sidebar freesits={state.Display.freesits} reservedSits={state.Display.reservedSits} dispatch={dispatch} />
            <Display modal={state.Modal} display={state.Display} dispatch={dispatch}/>
        </div>
    )
}

export default Main;