import s from "./sample.module.css";

const Sample = () => {

    return(
        <div className={s.sample}>
            <h1>Sample of our M*vies</h1>
            <p>
                Here you can see the Trailer for one of our movies. If you like it, so don't wait anymore!
                There are still free sits you can book for the movie!
            </p>
            <iframe width="560" height="315" src="https://www.youtube.com/embed/UQ-g0BdpbDM" title="YouTube video player"
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen>
            </iframe>
        </div>
    )
}

export default Sample;