import s from "./place.module.css";
import M from "materialize-css";

import {reserveSitActionCreater} from "../../../redux/display.reducer";

const Place = ({number, reserved, type, dispatch, booked}) => {

    const reservePlace = (e) => {
        if(e.target.dataset.booked !== "true") {
            const sitNumber = e.target.dataset.number;
            const isreserved = (e.target.dataset.reserved === "true");   // weil in einem html element alles zu einem String wird.
            console.log("sit number", sitNumber);
            const action = reserveSitActionCreater(sitNumber);
            console.log("@ Place, action", action);
            console.log("reserved?", isreserved);

            M.toast(isreserved ? {html: `Dear guest, you have deleted your r*servation for the sit number ${sitNumber}`} : {html: `Dear guest, you have r*served the sit number ${sitNumber}`});

            dispatch(action);
        } else{
            M.toast({html: "this sit is already booked, you were to late (book another sit)"});
        }
    }

    return(
        <div className={s.place_container}>
            <div data-booked={booked} data-number={number} data-reserved={reserved} onClick={(e) => reservePlace(e)} className={`${reserved? s.place_reserved: s.place} ${type === "Parkett" && s.place_parkett}`}></div>
        </div>
    )
}

export default Place;