import s from "./dashboard.module.css";
import 'antd/dist/antd.css';
import React, { useState } from 'react';
import { resetAllActionCreater } from "../../redux/dashboard.reducer";
import { resetSitsActionCreater } from "../../redux/display.reducer";
import {Button, Modal, Switch} from 'antd';


const Dashboard = ({dashboard, dispatch}) => {
    {/*Modal and Button*/}
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [switchOne, setSwtitchOne] = useState(false);
    const [switchTwo, setSwitchTwo] = useState(false);
    const [switchThree, setSwitchThree] = useState(false);

    const onSwitchOne = (checked) => {
            setSwtitchOne(checked);
    };

    const onSwitchTwo = (checked) => {
            setSwitchTwo(checked);
    };

    const onSwitchThree = (checked) => {
            setSwitchThree(checked);
    }

    const showModal = () => {
        setIsModalOpen(true);
    };
    const handleOk = () => {
        setIsModalOpen(false);
    };
    const handleCancel = () => {
        setIsModalOpen(false);
    };
    {/*Modal and Button*/}


    console.log("dashboard @ dashboard.jsx", dashboard);
    const resetAllPlaces = () => {
        const action = resetSitsActionCreater();
        dispatch(action);
        handleCancel();
    }
    return(
        <div className={s.dashboard}>
            <h1>This is the Mater Board</h1>
            <div className={s.dashboard_display}>
                <div>Freie Plätze...{dashboard.reservedSits? 24 - Number(dashboard.reservedSits?.numbers.length): 24}</div>
                <div>Umsatz....{dashboard.turnover? dashboard.turnover + " Bucks" : "Es ist Obst im Haus"}</div>
            </div>
            <button onClick={showModal} className={s.button}>
                <p>Selbstzerstörung</p>
                <p>(reset places)</p>
            </button>
            <Modal title="Der Geheime Geheimknopf" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
                <div className={s.modal_content}>
                    <div className={s.switchers}>
                        <Switch onChange={onSwitchOne}/>
                        <Switch onChange={onSwitchTwo}/>
                        <Switch onChange={onSwitchThree}/>
                    </div>
                    <div>
                        {(!switchOne || !switchTwo || !switchThree) && <Button style={{backgroundColor: "red", fontWeight: "700"}} disabled={true} type="danger">RESET</Button>}
                        {(switchOne && switchTwo && switchThree) && <Button onClick={resetAllPlaces} style={{backgroundColor: "red", fontWeight: "700"}} disabled={false} type="danger">RESET</Button>}
                    </div>
                </div>
            </Modal>
        </div>
    )

}

export default Dashboard;