export const getApiResource =  async (data) => {
    console.log("get Api resource");
    const dataJson = JSON.stringify(data);
    const response = await fetch("https://kinohubserver-production.up.railway.app/bookings/create", {method: "POST", body: dataJson, headers: {"Content-Type": "application/json"}})
}

export const getAllOrders = async () => {
    console.log("getting all orders");
    const response = await fetch("https://kinohubserver-production.up.railway.app/bookings/getall");
    const data = await response.json();
    console.log("all orders", data);
}

export const updateDashboardState = async (data) => {
    console.log("updating dashboard db");
    const dataJson = JSON.stringify(data);
    const response = await fetch("https://kinohubserver-production.up.railway.app/dashboard/update", {method: "POST", body: dataJson, headers: {"Content-Type": "application/json"}});
}

export const getDashboardState = async () => {
    console.log("getting dashboard states");
    const response = await fetch("https://kinohubserver-production.up.railway.app/dashboard/getIt");
    const data = await response.json();
    return data;
}

export const resetAll = async () => {
    console.log("reseting all.....");
    const response = await fetch("https://kinohubserver-production.up.railway.app/dashboard/reset");
}