import Navbar from "./components/Navbar/Navbar";
import "./App.css";
import {getAllOrders, getDashboardState} from "./utils/network";
import {updateStateActionCreater} from "./redux/display.reducer";
import {updateDashboardStateActionCreater} from "./redux/dashboard.reducer";
import {Routes, Route, BrowserRouter as Router} from "react-router-dom";
import Main from "./components/MAIN/Main";
import Dashboard from "./components/Dashboard/Dashboard";
import {useEffect} from "react";
import Sample from "./components/Sample/Sample";

//getAllOrders();


function App({state, dispatch}) {


    useEffect(() => {

        const getDashboard = async () => {
                const dashBoardState = await getDashboardState();
                console.log("dashboardstate @ getDashboard", dashBoardState.data[0]);
                const action = updateStateActionCreater(dashBoardState);
                const action1 =  updateDashboardStateActionCreater(dashBoardState);
                dispatch(action);
                dispatch(action1);
        }
        getDashboard()

    }, []);

  return (
      <Router>
        <div className="App">
            <Navbar/>
            <Routes>
                <Route path="/" element={<Main state={state} dispatch={dispatch}/>} />
                <Route path="/master" element={<Dashboard dispatch={dispatch} dashboard={state.Dashboard}/>}/>
                <Route path="/sample" element={<Sample/>}/>
            </Routes>
        </div>
      </Router>
  );

}

export default App;
